























import router from "@/router";
import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";

@Component
export default class Pad extends Vue {
  selectedButton: string[] = [];
  n: string = "0";
  stringAdd(event: MouseEvent) {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent!;
    // this.selectedButton.push(input);
    // console.log(input);
    if (this.n.length === 13) {
      return;
    }
    if (this.n === "0") {
      //一开始的状态this.n必为0
      if ("0123456789".indexOf(input) >= 0) {
        this.n = input;
      } else {
        this.n += input;
      }
      return;
    }
    if (this.n.indexOf(".") >= 0 && input === ".") {
      return;
    }
    this.n += input;
  }
  remove() {
    if (this.n.length === 1) {
      this.n = "0";
    } else {
      this.n = this.n.slice(0, -1);
    }
  }
  clear() {
    this.n = "0";
  }
  percent() {
    const b: number = parseFloat(this.n);
    const c: number = b * 0.01;
    this.n = c.toString();
  }
  OK(value: string) {
    if (this.n === "0") {
      window.alert("金额不能为空，请重新输入");
      return;
    } else {
      this.$emit("update:value", this.n);
      this.$emit("submit", this.n);
    }
    this.n = "0";
  }
}
